<template>
  <headline h-title=''>
    <div class="setting-row">
      <span class="setting-inline-block-r20"></span>
      <select-component @onshift='this.changeStyle' :sdata='{none: "",solid:"", dashed:"",dotted:""}' vtype='style.Border.style' class="setting-inline-block"></select-component>
      <input-num :num-data='this.getStyleAge("style.Border.width")' vtype='style.Border.width' @onshift='this.changeStyle' class="setting-inline-block-l20"></input-num>
    </div>
    <div class="setting-row">
      <span class="setting-inline-block-r20"></span>
      <input-color :color-data='this.getStyleAge("style.Border.color")' vtype='style.Border.color' @onshift='this.changeStyle' class="setting-inline-block"/>
    </div>
    <div class="setting-row">
      <input-checkbox-right @onshift='this.changeStyle' :checked-ata="this.getStyleAge('style.BoxShadow.show')" :info='{title:"",vtype:"style.BoxShadow.show"}' class="setting-inline-block"/>
      <div class="setting-embed-l10" v-show="getStyleAge('style.BoxShadow.show')">
        <input-num :num-data='this.getStyleAge("style.BoxShadow.radius")' vtype='style.BoxShadow.radius' @onshift='this.changeStyle' class="setting-inline-block"></input-num>
        <input-color @onshift='this.changeStyle' :color-ata="this.getStyleAge('style.BoxShadow.color')" vtype="style.BoxShadow.color" class="setting-inline-block-l10"></input-color>
        <div class="setting-embed">
          <input-num :num-data='this.getStyleAge("style.BoxShadow.level")' vtype='style.BoxShadow.level' @onshift='this.changeStyle' class="setting-inline-block">X</input-num>
          <input-num :num-data='this.getStyleAge("style.BoxShadow.vertical")' vtype='style.BoxShadow.vertical' @onshift='this.changeStyle' class="setting-inline-block-l10">Y</input-num>
        </div>
        <input-num class="setting-embed" :num-data='this.getStyleAge("style.BoxShadow.extension")' vtype='style.BoxShadow.extension' @onshift='this.changeStyle'></input-num>
      </div>
    </div>
  </headline>
</template>
<script>
import headline from './headline'
import inputCheckbox from './inputCheckbox'
import selectComponent from './selectComponent'
import inputRadio from './inputRadio'
import inputNum from './inputNum'
import inputColor from './inputColor'
import inputCheckboxRight from './inputCheckboxRight'
import {mapGetters, mapActions} from 'Vuex'
export default {
  name: 'borderSet',
  computed: {
    ...mapGetters([
      'getStyleAge'
    ])
  },
  methods: {
    ...mapActions([
      'changeStyle'
    ])
  },
  components: {
    inputCheckbox,
    selectComponent,
    inputRadio,
    inputNum,
    inputColor,
    headline,
    inputCheckboxRight
  }
}
</script>



